import React, { useEffect } from "react";
import {
  CCol,
  CModal,
  CModalBody,
  CModalHeader,
  CModalTitle,
  CRow,
} from "@coreui/react";
import BarcodeScannerComponent from "react-webcam-barcode-scanner";

const Scanner = ({showScanner, setShowScanner,setSearch}) => {
  const dimention = document.documentElement.clientHeight
  const height = dimention - 300
  const [ data, setData ] = React.useState('Not Found');
  console.log(data);
  return (
    <CRow className={showScanner ? null : 'd-none'}>
      <CCol>
        <CModal
          show={showScanner}
          closeOnBackdrop={false}
        >
          <CModalHeader className="px-4 pt-5 border-none">
            <CModalTitle className="text-white LatoBold " onClick={() => setShowScanner(false)}>Cancel</CModalTitle>
          </CModalHeader>
          <CModalBody className="px-4 py-0" style={{ minHeight: height }}>
          <BarcodeScannerComponent
            width={500}
            height={500}
            onUpdate={(err, result) => {
                alert()
                if (result) setData(result.text)
                else setData('Not Found')
              }}
            />
          </CModalBody>
        </CModal>
      </CCol>
    </CRow>
  );
};

export default Scanner
